<template>
    <nav id="navigationBar">
        <router-link class="navigationBarLogo" to="/"><h1>Viewfy</h1></router-link>
            <div @click="changeTheme">
                <i class="fas fa-lightbulb" id="lightBulb" ></i>
            </div>
            <div class="navigationLinks">
                <p v-if="isLoggedIn">
                    <router-link to="/home">Foldal</router-link>
                </p>

<!--                <p v-if="isLoggedIn">-->
<!--                    <router-link to="/explore">Felfedezs</router-link>-->
<!--                </p>-->

                <p v-if="isLoggedIn">
                    <router-link to="/profilesettings">Belltsok</router-link>
                </p>

                <p v-if="isLoggedIn">
                    <router-link to="/createpost">Bejegyzs</router-link>
                </p>

<!--                <p v-if="isLoggedIn">-->
<!--                    <router-link to="/createwhisper">Kzeli</router-link>-->
<!--                </p>-->

<!--                <p v-if="isLoggedIn">-->
<!--                    <router-link to="/chat">Chat</router-link>-->
<!--                </p>-->

                <p v-if="!isLoggedIn">
                    <router-link to="/login">Bejelentkezs</router-link>
                </p>

                <p v-if="!isLoggedIn">
                    <router-link to="/register">Regisztrci</router-link>
                </p>

                <p v-if="isLoggedIn">
                    <router-link to="/profile">Profil</router-link>
                </p>

                <p v-if="isLoggedIn">
                    <a to="/logout" @click.prevent="logoutUser">Kijelentkezs</a>
                </p>
            </div>
    </nav>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import fontawesome from '@fortawesome/fontawesome';
import brands from '@fortawesome/fontawesome-free-brands';
import solid from '@fortawesome/fontawesome-free-solid';

fontawesome.library.add(brands, solid);
export default {
    data() {
        return {
            lightModeEnabled: false,
        };
    },
    mounted: function () {
        // lekri a tmt a loklis trhelybl
        this.getThemeFromLocalStorage();
    },
    computed: {
        ...mapGetters(['isLoggedIn'])
    },
    methods: {
        clearErrorMessages() {
        let focusedElement = document.getElementById('errorMessages');
        focusedElement.innerHTML = '';
        },
        // mindezek a mapActions s a fenti mapGetters megtallhat UserREST.js alatt a raktr mappban az gyfl szl knyvtr
        ...mapActions(['logout']),
        logoutUser() {
            this.logout();
        },

        getThemeFromLocalStorage: function() {
            // elmenti a tmt loklisan
            const currentTheme = localStorage.getItem('theme');

            if (currentTheme) {
                document.documentElement.setAttribute('data-theme', currentTheme);
                this.lightModeEnabled = true;
                if (currentTheme === 'dark') {
                    document.documentElement.setAttribute('data-theme', currentTheme);
                    this.lightModeEnabled = false;
                }
            }
        },
        changeTheme: function() {
            if(this.lightModeEnabled) {
                document.documentElement.setAttribute('data-theme', 'dark');
                localStorage.setItem('theme', 'dark');
                this.lightModeEnabled = false;
            } else if(!this.lightModeEnabled) {
                document.documentElement.setAttribute('data-theme', 'light');
                localStorage.setItem('theme', 'light');
                this.lightModeEnabled = true;
            }
        },
    }
}
</script>

